import React, { Component } from 'react';

class Forecastday extends Component {
    constructor(props) {
        super(props);
        this.state = {  }
    }
    render() { 
        const { day } = this.props;
        if (!day) return null;
        return ( 
            <div className="forecastday-container">
                <div className="forecastDate">{day.date}</div>
                <div className="forecastIcon"><img src={day.condition.icon} alt="weather_icon" /></div>
                <div className="forecastTemp">Hi: {day.maxtemp_f}</div>
                <div className="forecastTemp">Lo: {day.mintemp_f}</div>
                <div className="forecastCondition">{day.condition.text}</div>
            </div>
         );
    }
}
 
export default Forecastday;