import React, { Component } from 'react';

class Weather extends Component {
    constructor(props) {
        super(props);
    }

    render() { 
        const { text, icon, temp, updated } = this.props;

        return (
          <div className="weatherWidget">
                <div className="condition">{text}</div>
                <div className="iconWeather"><img src={icon} alt="weather_icon"/></div>
                <div className="temp">{temp}Â°</div>
                <div className="updated">{updated}</div>
          </div>
        );
    }
}
 
export default Weather;