
// Weather app created by Tamara Roggow

import React, { Component } from 'react';
import "./sass/app.scss";
import "bootstrap/dist/css/bootstrap.min.css";
import axios from "axios";

import Bottomweather from './components/bottom'
import Titles from "./components/titles"
import Summary from "./components/weathersummary";
import Weather from "./components/weather"

const API_KEY = "bfb1fc18836b4d14800213219191303";

// Parent Component
class App extends Component {
  constructor(props) {
    super(props);
// field names used throughout components
    this.state = {
      cityName: "Burnsville, nc",
      region: undefined,
      numforecastDays: 5,
      temp: undefined,
      icon: undefined,
      updated: undefined,
      text: undefined,
      isLoading: true  // initiated to true on load
    }
  }
// fetches weather from Apixu.com and sets the state for Parent component
  updateWeatherApp() {
    const { cityName, numforecastDays } = this.state;
    const URL =
      `http://api.apixu.com/v1/forecast.json?key=${API_KEY}&q=${cityName}&${numforecastDays}`;
    axios.get(URL)
      .then(res => {
        return res.data;
      })
      .then(data => {
        this.setState({ isLoading: false, 
          temp: data.current.temp_f, 
          text: data.current.condition.text, 
          cityName: data.location.name, 
          region: data.location.region, 
          updated: data.current.last_updated, 
          icon: data.current.condition.icon, 
          forecastdays: data.forecast.forecastday })  // ERROR - fetching only first day from forecast day
      })
// needed for error output and logging
      .catch((err) => {
        if (err)
          console.error("Cannot fetch Weather Data from API,", err);
      })
  }
// gets function "updateWeather" from titles.jsx
  componentDidMount() {
    const { eventEmitter } = this.props;
// gets function updateWeatherApp from above and...
    this.updateWeatherApp();

    eventEmitter.on("updateWeather", (data) => {
// sets the state based upon user input from "submitLocation" function in titles.jsx
      this.setState({ cityName: data }, () => this.updateWeatherApp())
    })
  }

  render() {

    const { isLoading, text, temp, icon, updated, cityName, region, forecastdays } = this.state;
    const { eventEmitter } = this.props;
// mainDiv div contains entire output rendered --- used to style app relative to page it's rendered on
// weatherMain div contains all components --- used to style all components in conjuction
    return (
      <div className="container mainDiv">
        <div className="weatherMain">
          {isLoading && <h3>Weather is loading...</h3>} 
          {!isLoading &&
          <div>
            <Titles 
              cityName={cityName}
              region={region}
              eventEmitter={this.props.eventEmitter} />
          </div>}
          <div>
            <Summary />
            <Weather
              text={text}
              icon={icon}
              temp={temp}
              updated={updated}
              error={this.state.error}
            />
          </div>
          <div className="bottom">
            <Bottomweather forecastdays={forecastdays}/>
          </div>
        </div>
      </div>
    );
  }
}
 
export default App;
