import React, { Component } from 'react';

class Titles extends Component {

  onLocationUpdate(e) {
    this.setState({ locationName: e.target.value })
  }

  submitLocation() {
    const { locationName } = this.state;
    const { eventEmitter } = this.props;

    eventEmitter.emit("updateWeather", locationName);
  }

  state = {  }
  render() { 
    const { submitLocation } = this.state;
    const { cityName, region } = this.props;

    return ( 
      <div className="topTitle" >
        <h1 className="title">Current Weather for {cityName}, {region}</h1>
        <div>
          <input
            className="inputText"
            type="text"
            name="city"
            id="city"
            placeholder="City, State"
            onChange={this.onLocationUpdate.bind(this)}
          />
          <button className="myButton" onClick={this.submitLocation.bind(this)} >Get Weather</button>
        </div>
      </div>
     );
  }
}
 
export default Titles;
